
.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 40vmin;
  pointer-events: none;
}

body {
  background-color: rgb(62, 42, 173);
  background-image: url(/static/media/Background.919a2eae.jpg);
}

img {
  display: block;
  margin-left: auto;
  margin-right: auto;

  border: 3px solid rgb(19, 4, 83);
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.background {
  background-repeat: repeat-y;
  width: 100%;
}

.logo {
  background-color: black;
}

.weather {
  clear: both;
}

.weatherword {
  margin: 0 10px;
  font-size: 70px;
  font-style: italic;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-color: rgb(39, 84, 180);
  text-align: center;
  border: 3px solid rgb(19, 4, 83);
  display: grid;
  grid-gap: 10px;
  margin: 20px;
}

.button {
  background-color: #4e528d;
  border-radius: 15px;
  color: white;
  padding: 32px;
  text-align: center;
  font-size: 25px;
  margin: 10px 2px;
  margin-left: 10px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  text-decoration: none;
  cursor: pointer;
  width: 32%;
}

.button:hover {
  color: #000;
}

.homediv {
  font-size: 2em;
  font-family: Helvetica;
  font-weight: Bold;
  color: #444444;
  background-color: azure;
  text-align: center;
}

.fivedaydiv {
  width: 50%;
  margin: 5px;
  padding: 1em;
  background: rgba(255, 255, 255, 0.589);
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 3px solid rgb(19, 4, 83);
}

.carddiv {
  background-color: gray;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  text-align: center;
  border: 3px solid rgb(19, 4, 83);
}

.day {
  font-size: 35px;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: rgb(151, 12, 12);
  margin: 20px 2px;
}

.temperature {
  font-size: 2em;
  border: 2px #ffffff;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}

.fivedays {
  border-style: solid;
  background-color: #4e528d;
  text-align: center;
  font-size: 75px;
  margin: 10px 2px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  text-decoration: none;
  border: 4px solid black;
}

.img {
  margin-bottom: 20px;
}

.api {
  text-align: center;
  font-size: 3em;
}
@media only screen and (max-width: 600px) {
  .button {
    background-color: #4e528d;
  color: white;
  padding: 32px;
  text-align: center;
  font-size: 10px;
  margin: 10px 2px;
  }
}
.api {
  text-align: center;
  font-size: 2em;
}
.img {
  height: 150px;
  width: 75%;
}


.fivedays {
  border-style: solid;
  background-color: #4e528d;
  text-align: center;
  font-size: 50px;
  margin: 10px 2px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  text-decoration: none;
  border: 4px solid black;
}
.temperature {
  font-size: 1em;
  border: 2px #ffffff;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
}
